<template>
  <div class="main">
    <el-container>
      <el-header>
        <div class="header">
          <div class="header_left">
            <div class="logoBox" @click="openHome" title="我的收藏">
              <img src="../assets/img/favicon.png">
              <h1>兜兜的导航</h1>
            </div>
            <div class="timeBox">
              <span>
                {{ realTime.Month }} <b>月</b> {{ realTime.day }} <b>日 {{ realTime.Hour }}:{{ realTime.Minute }}:{{
                  realTime.seconds }}</b>
              </span>
            </div>
          </div>
          <div class="header_right">
            <button @click="openQrCodeDialog">推荐网址</button>
          </div>
        </div>
      </el-header>
      <el-container>
        <el-aside>
          <el-scrollbar>
            <div class="menu">
              <div class="menuBoxBar" :style="{ transform: `translateY(${LineTop}px)` }"></div>
              <div :class="`menuBox ${index === menuActiverCode ? 'active' : ''}`" v-for="(item, index) in arrey"
                @click="active(index, item)">
                {{ item.title }}
              </div>
            </div>
          </el-scrollbar>
        </el-aside>
        <el-main>
          <el-backtop target=".scroll" :right="100" :bottom="100" />
          <el-scrollbar class="scroll" ref="scrollbarRef">
            <div class="container">
              <div class="container_box search">
                <div class="search_box">
                  <div class="header">
                    <h1>搜索</h1>
                  </div>
                  <div class="body" title="浅浅搜一下">
                    <input type="text" v-model="searchValue" placeholder="我来帮你百度一下" @keyup.enter="search">
                    <button @click="search"><el-icon>
                        <Search />
                      </el-icon></button>
                  </div>
                </div>
              </div>
              <div :class="`container_box ${menuActiverIndex == index ? 'active' : ''}`" v-for="(item, index) in arrey">
                <div class="boxHeader" :id="item.key">
                  {{ item.title }}
                </div>
                <div class="boxBody">
                  <div class="boxBody_webbox" v-for="(res, index) in item.childers">
                    <a :href="res.url" target="_blank" :title="res.jianjie">
                      <div class="webBox_right">
                        <img :src="res.imgUrl" :title="res.webName">
                      </div>
                      <div class="webBox_left">
                        <div class="webBox_left_header">
                          {{ res.webName }}
                        </div>
                        <div class="webBox_left_body">
                          {{ res.jianjie }}
                        </div>
                      </div>
                    </a>
                    <div class="webBox_Add" @click="Add(res)">
                      <el-icon>
                        <Plus />
                      </el-icon>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </el-scrollbar>
        </el-main>
      </el-container>
    </el-container>
    <el-dialog v-model="categoricalData.show" title="你需要添加该网址到哪个分类下?" width="350">
      <div style="height: 100px;">
        <div style="margin-bottom: 36px;">
          分类：
          <el-select v-model="categoricalData.selectValue">
            <el-option v-for=" item in categoricalData.selectData" :label="item.title" :value="item.value" />
          </el-select>
        </div>
        <div style="float: right;">
          <el-button @click="categoricalData.show = false">取消</el-button>
          <el-button type="primary" @click="sureToAdd(categoricalData.selectValue)">确定</el-button>
        </div>
      </div>
    </el-dialog>
  </div>
</template>
<script setup>
import webData from '../untils/websiteData.js';
import { ref, onMounted, computed, reactive, nextTick, watchEffect, toRef, toRefs, watch, onUnmounted, h } from 'vue';
import { ElMessageBox, ElMessage, ElNotification } from 'element-plus'
import { useRouter } from 'vue-router'
import IndexDB from '../untils/indexDB.js'
import dayjs from 'dayjs';
const router = useRouter()
const openHome = () => {
  let result = '/homeson'
  router.push(result)
}
const realTime = reactive({})
setInterval(() => {
  realTime.Month = dayjs().format('MM');
  realTime.day = dayjs().format('DD');
  realTime.Hour = dayjs().format('HH');
  realTime.Minute = dayjs().format('mm');
  realTime.seconds = dayjs().format('ss')
}, 100);
//#region 初始化数据

const arrey = webData.webArry

const recommendData = randomAcquisition(arrey)
function randomAcquisition(data) {
  let allData = data.reduce((e, item) => {
    return e = [...e, ...item.childers]
  }, [])
  const top20Items = allData.sort((a, b) => b.metaDataId - a.metaDataId).slice(0, 10)
  let newSort = {
    title: '近期更新',
    key: 'recommend',
    childers: top20Items
  }
  return newSort
}
if (arrey[0].title !== '近期更新') {
  arrey.unshift(recommendData)
}

//#endregion
//#region 左侧菜单选中效果
const menuActiverCode = ref(0)
const menuActiverIndex = ref()
const scrollbarRef = ref(null)
const LineTop = ref(menuActiverCode.value * 30)
let time = null
function active(index, item) {
  const jump = document.querySelector(`#${item.key}`)
  const total = jump.offsetTop
  scrollbarRef.value.scrollTo({
    top: total - 30,
    behavior: "smooth",
  })
  menuActiverCode.value = index
  menuActiverIndex.value = index

  if (time) clearTimeout(time)
  time = setTimeout(() => {
    menuActiverIndex.value = undefined
  }, 3000)

  LineTop.value = index * 30
}
//#endregion
//#region 搜索 
const searchValue = ref('')
function search(e) {
  const searchUrl = 'https://www.baidu.com/s?wd=' + searchValue.value
  window.open(searchUrl, '_blank');
}
//#endregion

//#region 点击新增按钮
const dbName = 'DouDouDB', storeName = 'website';
const categoricalData = reactive({
  show: false,
})
let Database
async function Add(item) {
  categoricalData.show = true;
  categoricalData.row = item
  const db = await IndexDB.openDB(dbName, storeName, 1);
  Database = db
  const webDatas = await IndexDB.getDataByKey(db, storeName, 1);
  // 如果数据库中有值
  if (webDatas) {
    const data = JSON.parse(webDatas.data);
    const selectData = data.map((item, index) => { // 下拉框数据
      return {
        'title': item.title,
        'value': item.key
      }
    })
    categoricalData.selectData = selectData
    categoricalData.selectValue = selectData[0].value
    categoricalData.webData = data

  } else {
    const selectData = arrey.map((item, index) => { // 下拉框数据
      if (index) {
        return {
          'title': item.title,
          'value': item.key
        }
      }
    }).filter(Boolean)
    categoricalData.selectData = selectData
    categoricalData.selectValue = selectData[0].value
    categoricalData.webData = JSON.parse(JSON.stringify(arrey.slice(1)))
  }
}
async function sureToAdd(value) {
  let index = categoricalData.webData.findIndex(item => item.key == value)
  categoricalData.row.metaDataId = new Date().getTime();
  categoricalData.webData[index].childers.push(categoricalData.row)
  const newData = { id: 1, data: JSON.stringify(categoricalData.webData) };
  IndexDB.updateDB(Database, storeName, newData);
  ElMessage({
    message: '添加收藏成功',
    type: 'success',
  })
  categoricalData.show = false;
}
//#endregion
//#region 打开二维码弹窗
const openQrCodeDialog = () => {
  ElMessageBox.alert(
    `<div class="QrCode"></div>`,
    {
      // autofocus: false,
      dangerouslyUseHTMLString: true,
      callback: () => { },
    })
}
//#endregion
</script>

<style lang="less" scoped>
