  height: 100vh;
  background-color: white;

  .el-container {
    height: 100%;

    .el-header {
      height: 60px;
      padding: 0 40px;
      border-bottom: 1px solid #dcdfe6;

      .header {
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .header_left {
          display: flex;
          align-items: center;

          .logoBox {
            height: 100%;
            display: flex;
            align-items: center;
            cursor: pointer;
            transition: all 1s;

            &:hover {
              h1 {
                color: #409EFF;
              }
            }

            img {
              width: 40px;
              height: 40px;
            }

            h1 {
              margin: 0;
              margin-left: 10px;
              font-size: 20px;
              color: #303133;
              transition: all 1s;
            }
          }

          .timeBox {
            margin-left: 20px;

            b {
              font-size: 12px;
            }
          }
        }

        .header_right {
          button {
            background-color: #409EFF;
            color: white;
            border: none;
            padding: 5px 10px;
            border-radius: 20px;
            font-size: 14px;
            cursor: pointer;

            &:hover,
            &:active {
              background-color: #398ee2;
            }
          }
        }
      }

    }

    .el-container {
      height: calc(100% - 60px);
      background-color: #f5f5f5;

      .el-aside {
        width: 200px;
        transition: all .5s;

        .menu {
          position: relative;
          overflow: hidden;
          padding: 20px 40px;

          .menuBoxBar {
