            height: 15px;
            margin: 7.5px 0;
            background-color: #409EFF;
            border-radius: 20px;
            transition: all .5s ease-in-out;
            left: 37px;
          }

          .menuBox {
            line-height: 30px;
            cursor: pointer;
            transition: all .8s;

            &:hover {
              color: #409EFF;
            }
          }

