              color: #303133;
              transition: all .5s;
            }

            .boxBody {
              // display: flex;

              .boxBody_webbox {
                width: calc(16.666666666666666% - 20px);
                float: left;
                align-items: center;
                margin: 0 10px;
                border-radius: 10px;
                transition: all .2s ease-in;
                position: relative;

                &:hover {
                  box-shadow: 0px 0px 12px rgba(0, 0, 0, .12);
                  transform: translateY(-5px);

                  .webBox_Add {
                    right: 20px;
                    opacity: 1;
                    transform: translateX(20px), translateY(-50%);
                  }
                }

                .webBox_Add {
                  position: absolute;
                  right: 40px;
                  opacity: 0;
                  transition: all .5s;
                  top: 50%;
                  transform: translateY(-50%);
                  border-radius: 50%;
                  vertical-align: bottom;
                  display: flex;

                  &:hover {
                    background-color: #398ee2;
                    color: white;
                    transform: scale(1.2) translateY(-40%);
                    cursor: pointer;
                  }
                }

                a {
                  width: 100%;
                  height: 70px;
                  display: block;
                  position: relative;

                  .webBox_right {
                    width: 40px;
                    position: relative;
                    float: left;
                    height: 100%;

                    img {
                      width: 30px;
                      height: 30px;
                      padding: 4px;
                      position: absolute;
                      top: 50%;
                      left: 50%;
                      transform: translateY(-50%) translateX(-50%);
                      border-radius: 50%;
                      overflow: hidden;
                    }
                  }

                  .webBox_left {
                    width: auto;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    position: absolute;
                    top: 50%;
                    left: 40px;
                    transform: translateY(-50%);
                    width: calc(100% - 40px);

                    .webBox_left_header {
                      font-size: 16px;
                      margin-bottom: 5px;
                      transition: all 0.5s;
                    }

                    .webBox_left_body {
                      font-size: 12px;
                      transition: all 0.5s;
                      overflow: hidden;
                      text-overflow: ellipsis;
                    }

                  }
                }



              }
            }
          }

          .active {
            border-color: #398ee2;
          }

          .search {
            height: 300px;
            background-image: url('../assets/img/findView.jpg');
            background-position: center 70%;
            background-size: cover;
            background-repeat: no-repeat;
            position: relative;
            transition: all .5s;
            border: none;

            .search_box {
              width: 100%;
              position: absolute;
              z-index: 999;
              top: 50px;
              left: 50%;
              transition: all .5s;
              transform: translateX(-50%);
              max-width: 800px;


              // border: 1px solid ;
              .header {
                h1 {
                  text-align: center;
                  color: white;
                  font-weight: normal;
                  transition: all .5s;
                }
              }


              .body {
                width: 95%;
                background-color: rgba(0, 0, 0, .5);
                border-radius: 50px;
                position: relative;
                backdrop-filter: blur(18px);
                margin: 0 auto;

                input {
                  padding: 9px 20px;
                  background-color: transparent;
                  border: none;
                  outline: none;
                  // line-height: ;
                  height: 50px;
                  width: 100%;
                  color: white;

                  &::-webkit-input-placeholder {
                    color: #909399;
                  }
                }

                button {
                  position: absolute;
                  right: 20px;
                  top: 50%;
                  transform: translateY(-50%);
                  background-color: transparent;
                  border: none;
                  color: white;
                  font-size: 20px;
                  font-weight: bold;
                  cursor: pointer;

                }
              }

            }

            &::after {
              width: 100%;
              height: 100%;
              content: '';
              background-color: rgba(0, 0, 0, .1);
              position: absolute;
              top: 0;
              left: 0;
            }
          }
        }
      }

    }

  }
}

